import { useState, useEffect } from 'react';

// material
import {
  Box,
  Container,
  Typography,
  styled,
  Paper,
  Grid,
  Button,
  Stack,
  LinearProgress,
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  DialogContentText,
  List,
  ListItem
} from '@material-ui/core';

import { useQuery } from '@apollo/client';
import { motion } from 'framer-motion';
import { useTheme } from '@material-ui/core/styles';

import { useEthers, useTokenBalance, useCalls, useBlockNumber, useEtherBalance } from '@usedapp/core';
import { utils, Contract } from 'ethers';

// hooks
import useSettings from '../hooks/useSettings';
import useCountdown from '../hooks/useCountdown';

// components
import Page from '../components/Page';
import { varFadeIn, varFadeInUp, varWrapEnter, varFadeInRight } from '../components/animate';
import Staking from '../components/Staking';
import {
  LOTO_STAKING_ADDRESS,
  LOTO_STAKING_INTERFACE,
  LOTO_BNB_STORAGE_ADDRESS,
  LOTO_BNB_STORAGE_INTERFACE,
  LOTO_TOKEN_ADDRESS,
  SHAZU_TOKEN_ADDRESS
} from '../config';
import { fCRCurrency } from '../utils/formatNumber';
import ClaimButton from '../components/ClaimButton';
import ClaimButtonWithReward from '../components/ClaimButtonWithReward';
import { GET_TOTAL_DROPS } from '../queries';

// ----------------------------------------------------------------------

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(2),
  color: theme.palette.text.primary,
  height: '100%'
}));

const CountdownStyle = styled('div')({
  display: 'flex',
  justifyContent: 'left'
});

const SeparatorStyle = styled(Typography)(({ theme }) => ({
  margin: theme.spacing(0, 1),
  [theme.breakpoints.up('sm')]: {
    margin: theme.spacing(0, 1.5)
  }
}));

const AnimateContainer = styled(motion.div)(({ theme }) => ({}));

const startCountdown = new Date();

const ListWrapperStyle = styled(Paper)(({ theme }) => ({
  width: '100%',
  border: `solid 1px ${theme.palette.divider}`,
  background: 'none'
}));

export default function StakePage() {
  const { account } = useEthers();

  const balance = useTokenBalance(LOTO_STAKING_ADDRESS, account);

  const [dropBalance, setDropBalance] = useState('0');

  const [timeLock, setTimeLock] = useState(0);

  const [freePeriod, setFreePeriod] = useState(0);

  const [bnbDropBalance, setBNBDropBalance] = useState('0');

  const [juiceDropBalance, setJuiceDropBalance] = useState('0');

  const [shazuDropBalance, setShazuDropBalance] = useState('0');

  const [lockUnstake, setLockUnstake] = useState(false);

  const blockNumber = useBlockNumber();

  // Success or Failed Dialog
  const [openStakeStatus, setOpenStakeStatus] = useState(false);
  const [stakeStatusMsg, setStatusMsg] = useState({ message: '', icon: '' });
  const handleCloseStakeStatus = () => {
    setOpenStakeStatus(false);
  };

  const failedStakeStatusMsg = (msg) => {
    setStatusMsg({ message: msg, icon: 'failedWarning.svg', title: 'Failed' });
    setOpenStakeStatus(true);
  };

  const successStakeStatusMsg = (msg) => {
    setStatusMsg({ message: msg, icon: 'success.svg', title: 'Success' });
    setOpenStakeStatus(true);
  };

  useEffect(() => {
    if (balance) {
      setDropBalance(utils.formatEther(balance.toString()));
    } else {
      setDropBalance('0');
    }
  }, [account, balance]);

  const dropData = useCalls([
    {
      contract: new Contract(LOTO_STAKING_ADDRESS, LOTO_STAKING_INTERFACE),
      method: 'timelock',
      args: []
    },
    {
      contract: new Contract(LOTO_STAKING_ADDRESS, LOTO_STAKING_INTERFACE),
      method: 'getRewardsAmount',
      args: [account]
    },
    {
      contract: new Contract(LOTO_BNB_STORAGE_ADDRESS, LOTO_BNB_STORAGE_INTERFACE),
      method: 'freePeriod',
      args: []
    },
    {
      contract: new Contract(LOTO_STAKING_ADDRESS, LOTO_STAKING_INTERFACE),
      method: 'getRewardsAmountWithToken',
      args: [LOTO_TOKEN_ADDRESS, account]
    },
    {
      contract: new Contract(LOTO_STAKING_ADDRESS, LOTO_STAKING_INTERFACE),
      method: 'getRewardsAmountWithToken',
      args: [SHAZU_TOKEN_ADDRESS, account]
    }
  ]);

  useEffect(() => {
    if (dropData && dropData[0] && dropData[2] && dropData[0].value && dropData[2].value) {
      setTimeLock(dropData[0].value[0].toNumber() + dropData[2].value[0].toNumber());

      setFreePeriod(dropData[2].value[0].toNumber());
    }

    if (dropData && dropData[1] && dropData[1].value && dropData[1].value.amountToClaim) {
      if (dropData[1].value.amountToClaim.gt(utils.parseEther('0.000000009'))) {
        setBNBDropBalance(utils.formatEther(dropData[1].value.amountToClaim.toString()));
      } else {
        setBNBDropBalance(0.0);
      }
    }

    if (dropData && dropData[3] && dropData[3].value && dropData[3].value.amountToClaim) {
      if (dropData[3].value.amountToClaim.gt(utils.parseEther('0.000000009'))) {
        setJuiceDropBalance(utils.formatEther(dropData[3].value.amountToClaim.toString()));
      } else {
        setJuiceDropBalance(0.0);
      }
    }


    if (dropData && dropData[4] && dropData[4].value && dropData[4].value.amountToClaim) {
      if (dropData[4].value.amountToClaim.gt(utils.parseEther('0.000000009'))) {
        setShazuDropBalance(utils.formatEther(dropData[4].value.amountToClaim.toString()));
      } else {
        setShazuDropBalance(0.0);
      }
    }
  }, [dropData, account]);

  const [total, setTotal] = useState(0);
  const [totalJuiceDrops, setTotalJuiceDrops] = useState(0);
  const [totalShazuDrops, setTotalShazuDrops] = useState(0);
  const { data } = useQuery(GET_TOTAL_DROPS, {
    pollInterval: 3000
  });

  useEffect(() => {
    console.log(data);
    if (data && data.drops && data.drops[0]) {
      setTotal(data.drops[0].totalDrops);
      setTotalJuiceDrops(data.drops[0].totalJuiceDrops);
      setTotalShazuDrops(data.drops[0].totalShazuDrops);
    }
  }, [data]);

  const storageBalance = useEtherBalance(LOTO_BNB_STORAGE_ADDRESS);

  const storageJuiceBalance = useTokenBalance(LOTO_TOKEN_ADDRESS, LOTO_BNB_STORAGE_ADDRESS);

  const storageShazuBalance = useTokenBalance(SHAZU_TOKEN_ADDRESS, LOTO_BNB_STORAGE_ADDRESS);

  const timelockDate = new Date(timeLock * 1000);
  const finishCountdown = new Date(timeLock * 1000);

  const theme = useTheme();
  const { themeStretch } = useSettings();

  let progressPCT = 0;

  if (new Date().getTime() <= finishCountdown.getTime() && timeLock !== 0) {
    progressPCT = ((Date.now() - startCountdown) / (finishCountdown - startCountdown)) * 100;
  } else {
    progressPCT = 0;
  }

  const countdown = useCountdown(finishCountdown);

  const progress = progressPCT;

  useEffect(() => {
    if (new Date().getTime() <= timelockDate.getTime() - freePeriod * 1000 && timeLock !== 0) {
      setLockUnstake(true);
    } else {
      setLockUnstake(false);
    }
  }, [startCountdown, timelockDate, blockNumber]);

  return (
    <Page title="Cryptodrop | Stake" style={{ overflow: 'hidden' }}>
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <AnimateContainer initial="initial" animate="animate" variants={varWrapEnter}>
          <motion.div variants={varFadeIn}>
            <img
              src="/static/svg/staking.svg"
              alt=""
              style={{
                position: 'absolute',
                opacity: '0.15',
                marginTop: '-1.5rem',
                marginLeft: '-1.5rem',
                zIndex: '-1'
              }}
            />
          </motion.div>
          <motion.div variants={varFadeInRight}>
            <Typography variant="h3" component="h1" paragraph>
              Staking
            </Typography>
          </motion.div>

          {account ? (
            <motion.div variants={varFadeInRight}>
              <Grid container spacing={2} direction="row" justifyContent="center" style={{ marginBottom: '2rem' }}>
                <Grid item xs={12} md={8} lg={8} xl={8}>
                  <Item
                    style={{
                      maxWidth: '800px',
                      minHeight: '12rem',
                      height: 'unset',
                      margin: 'auto',
                      padding: '1.5rem',
                      backgroundImage: 'url(/static/background/DropBanner.png)',
                      backgroundSize: 'cover',
                      boxShadow: theme.customShadows.primary,
                      border: '2px solid',
                      borderColor: theme.palette.primary.main
                    }}
                  >
                    <Stack style={{ height: '100%' }}>
                      <Stack className="staking-claim-stack">
                        <Stack className="claim-juice-stack" style={{ marginTop: '0'}}>
                          <Typography variant="h5">BNB Drop</Typography>

                          <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center">
                            <Typography variant="h4" style={{ fontWeight: 'normal' }}>
                              {bnbDropBalance > 0.00001 ? fCRCurrency(bnbDropBalance) : '0'}
                            </Typography>
                            <img alt="" src="/static/icons/ic_bnb.svg" width={20} height={20} />
                          </Stack>
                          <ClaimButton
                            drop={bnbDropBalance}
                            openSuccessPopup={successStakeStatusMsg}
                            openFailedPopup={failedStakeStatusMsg}
                          />
                        </Stack>

                        <Stack className="claim-juice-stack">
                          <Typography variant="h5">JUICE Drop</Typography>

                          <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center">
                            <Typography variant="h4" style={{ fontWeight: 'normal' }}>
                              {fCRCurrency(juiceDropBalance)}
                            </Typography>
                            <img alt="" src="/static/icons/ic_juice.svg" width={20} height={20} />
                          </Stack>
                          <ClaimButtonWithReward
                            drop={juiceDropBalance}
                            token={LOTO_TOKEN_ADDRESS}
                            tokenName={'JUICE'}
                            openSuccessPopup={successStakeStatusMsg}
                            openFailedPopup={failedStakeStatusMsg}
                          />
                        </Stack>

                        <Stack className="claim-juice-stack">
                          <Typography variant="h5">SHAZU Drop</Typography>

                          <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center">
                            <Typography variant="h4" style={{ fontWeight: 'normal' }}>
                            {fCRCurrency(shazuDropBalance)}
                            </Typography>
                            <img alt="" src="/static/icons/ic_shazu.png" width={20} height={20} />
                          </Stack>
                          <ClaimButtonWithReward
                            drop={shazuDropBalance}
                            token={SHAZU_TOKEN_ADDRESS}
                            tokenName={'SHAZU'}
                            openSuccessPopup={successStakeStatusMsg}
                            openFailedPopup={failedStakeStatusMsg}
                          />
                        </Stack>
                      </Stack>

                      {progress !== 0 && countdown.days >= 0 ? (
                        <>
                          <Typography variant="subtitle1" style={{ marginTop: '2.4rem' }}>
                            Next Drop:
                          </Typography>
                          <CountdownStyle>
                            <div>
                              <Typography variant="h6">{countdown.hours}</Typography>
                              <Typography sx={{ color: 'white', opacity: '0.5', marginTop: '-0.2rem' }}>
                                Hours
                              </Typography>
                            </div>

                            <SeparatorStyle variant="h6">:</SeparatorStyle>

                            <div>
                              <Typography variant="h6">{countdown.minutes}</Typography>
                              <Typography sx={{ color: 'white', opacity: '0.5', marginTop: '-0.2rem' }}>
                                Minutes
                              </Typography>
                            </div>

                            <SeparatorStyle variant="h6">:</SeparatorStyle>

                            <div>
                              <Typography variant="h6">{countdown.seconds}</Typography>
                              <Typography sx={{ color: 'white', opacity: '0.5', marginTop: '-0.2rem' }}>
                                Seconds
                              </Typography>
                            </div>
                          </CountdownStyle>

                          <Box sx={{ width: '90%', marginTop: '0.5rem', marginBottom: '1rem' }}>
                            <LinearProgress variant="determinate" value={progressPCT} />
                          </Box>
                        </>
                      ) : (
                        ''
                      )}

                      <Box style={{ position: 'relative', width: '100%', height: '0' }}>
                        <img
                          alt=""
                          src="/static/background/coinstack.png"
                          style={{
                            position: 'absolute',
                            right: '-3.5rem',
                            bottom: '-3.2rem',
                            width: '8rem',
                            height: '8rem'
                          }}
                        />
                      </Box>
                    </Stack>
                  </Item>
                </Grid>

                <Grid item xs={12} md={4} lg={4} xl={4}>
                  <Item
                    style={{
                      height: 'auto',
                      margin: 'auto',
                      padding: '1.5',
                      backgroundImage: 'url(/static/background/DropInfo.jpg)',
                      backgroundSize: 'cover',
                      boxShadow: theme.customShadows.primary,
                      border: '2px solid',
                      borderColor: theme.palette.primary.main
                    }}
                  >
                    <Stack>
                      <Typography variant="h5" style={{ marginBottom: '0.5rem' }}>
                        Drop Info{' '}
                        <span role="img" aria-label="drop-emoji">
                          💦
                        </span>
                      </Typography>
                      <ListWrapperStyle>
                        <List>
                          <ListItem>
                            <Stack>
                              <Stack direction="row" style={{ marginBottom: '0.4rem', flexWrap: 'wrap' }}>
                                <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center" style={{ marginRight: '1rem' }}>
                                  <Typography variant="h5" style={{ marginBottom: '-0.3rem' }}>
                                    {fCRCurrency(total)} BNB
                                  </Typography>
                                  <img
                                    alt=""
                                    src="/static/icons/ic_bnb.svg"
                                    style={{ width: '1.25rem', height: '1.25rem', marginLeft: '0.3rem' }}
                                  />
                                </Stack>

                                <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center" style={{ marginRight: '1rem' }}>
                                  <Typography variant="h5" style={{ marginBottom: '-0.3rem' }}>
                                    {fCRCurrency(totalJuiceDrops)} JUICE
                                  </Typography>
                                  <img
                                    alt=""
                                    src="/static/icons/ic_juice.svg"
                                    style={{ width: '1.25rem', height: '1.25rem', marginLeft: '0.3rem' }}
                                  />
                                </Stack>

                                <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center" style={{ marginRight: '1rem' }}>
                                  <Typography variant="h5" style={{ marginBottom: '-0.3rem' }}>
                                    {fCRCurrency(totalShazuDrops)} SHAZU
                                  </Typography>
                                  <img
                                    alt=""
                                    src="/static/icons/ic_shazu.png"
                                    style={{ width: '1.25rem', height: '1.25rem', marginLeft: '0.3rem' }}
                                  />
                                </Stack>
                              </Stack>
                              <Typography variant="p" style={{ opacity: '0.5' }}>
                                Total Drop Since Launched
                              </Typography>
                            </Stack>
                          </ListItem>
                          <ListItem>
                            <Stack>
                              <Stack direction="row" style={{ marginBottom: '0.4rem', flexWrap: 'wrap' }}>
                                <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center" style={{ marginRight: '1rem' }}>
                                  <Typography variant="h5" style={{ marginBottom: '-0.3rem' }}>
                                    {storageBalance ? fCRCurrency(utils.formatEther(storageBalance)) : 0} BNB
                                  </Typography>
                                  <img
                                    alt=""
                                    src="/static/icons/ic_bnb.svg"
                                    style={{ width: '1.25rem', height: '1.25rem', marginLeft: '0.3rem' }}
                                  />
                                </Stack>

                                <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center" style={{ marginRight: '1rem' }}>
                                  <Typography variant="h5" style={{ marginBottom: '-0.3rem' }}>
                                    {storageJuiceBalance ? fCRCurrency(utils.formatEther(storageJuiceBalance)) : 0}{' '}
                                    JUICE
                                  </Typography>
                                  <img
                                    alt=""
                                    src="/static/icons/ic_juice.svg"
                                    style={{ width: '1.25rem', height: '1.25rem', marginLeft: '0.3rem' }}
                                  />
                                </Stack>

                                <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center" style={{ marginRight: '1rem' }}>
                                  <Typography variant="h5" style={{ marginBottom: '-0.3rem' }}>
                                    {storageShazuBalance ? fCRCurrency(utils.formatEther(storageShazuBalance)) : 0}{' '}
                                    SHAZU
                                  </Typography>
                                  <img
                                    alt=""
                                    src="/static/icons/ic_shazu.png"
                                    style={{ width: '1.25rem', height: '1.25rem', marginLeft: '0.3rem' }}
                                  />
                                </Stack>
                              </Stack>
                              <Typography variant="p" style={{ opacity: '0.5' }}>
                                Next Drop Total
                              </Typography>
                            </Stack>
                          </ListItem>
                        </List>
                      </ListWrapperStyle>
                    </Stack>
                  </Item>
                </Grid>
              </Grid>
            </motion.div>
          ) : (
            ''
          )}

          <motion.div variants={varFadeInRight}>
            <Grid container spacing={2} alignItems="stretch" direction="row" justifyContent="flex-start">
              <Grid item xs={12} md={8} lg={8} xl={8}>
                <Item style={{ display: 'flex', flexDirection: 'column' }}>
                  <Staking lockUnstake={lockUnstake} timeLock={timeLock - freePeriod} />
                </Item>
              </Grid>

              <Grid item xs={12} md={4} lg={4} xl={4}>
                {/* Balance */}
                <Item
                  style={{
                    display: 'flex',
                    flexDirection: 'column',
                    height: '35%',
                    minHeight: '8rem',
                    backgroundImage: 'url(/static/background/balance.png)',
                    backgroundSize: 'cover'
                  }}
                >
                  <Typography variant="h5">Balance</Typography>

                  <Stack direction="row" spacing={1} justifyContent="flex-start" alignItems="center">
                    <Typography variant="h4" style={{ fontWeight: 'normal' }}>
                      {fCRCurrency(dropBalance)} $JUICE
                    </Typography>
                    <img alt="" src="/static/icons/ic_juice.svg" width={25} height={25} />
                  </Stack>
                </Item>
              </Grid>
            </Grid>
          </motion.div>

          <Dialog open={openStakeStatus} onClose={handleCloseStakeStatus}>
            <DialogTitle>{stakeStatusMsg.title}</DialogTitle>
            <DialogContent>
              <img
                alt=""
                src={`/static/svg/${stakeStatusMsg.icon}`}
                style={{ width: '8rem', margin: 'auto', marginTop: '2rem' }}
              />
              <DialogContentText
                id="alert-dialog-description"
                style={{ textAlign: 'center', marginTop: '2rem', fontSize: '1.2rem' }}
              >
                {stakeStatusMsg.message}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseStakeStatus} autoFocus>
                Ok
              </Button>
            </DialogActions>
          </Dialog>

          <img
            src="/static/background/waves.svg"
            alt=""
            style={{
              position: 'fixed',
              bottom: '0',
              left: '0',
              zIndex: '-1',
              opacity: '0.2',
              width: '100%',
              minHeight: '20rem',
              objectFit: 'cover'
            }}
          />
        </AnimateContainer>
      </Container>
    </Page>
  );
}
